import Helper from '@/function.js';
export default {
	data() {
		return {
			token: '',
			info: {},
			item: {},
			imgPath: "",
			imgSvr: "",
			sendType: 0,
			myTasklist: [],
			token: '',
			pageIndex: 1,
			pageSize: 10,
			contentText: {
				contentdown: '上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			},
			taskPackage: {},
			total: -1,
			nodata: false,
			isOk:"none",
			img:"",
			textTishi:""
		}

	},
	onLoad: function(option) {
		this.token = uni.getStorageSync('token');

		this.taskPackage.price = 0;
		this.taskPackage.quota = 0;
		this.taskPackage.useQuota = 0;

		this.taskPackage = JSON.parse(option.item);
		this.imgSvr = this.ImgServer;
		this.total = this.taskPackage.useQuota;							
	},
	onShow: function() {
		this.pageIndex = 1;
		this.myTasklist = [];
		this.selType = -1;

		this.token = uni.getStorageSync('token');
		this.userInfo = uni.getStorageSync('userInfo');
		this.getMyTaskList(this.taskPackage.id);

	},
	onPullDownRefresh: function() {
		this.pageIndex = 1;
		this.myTasklist = [];
		this.getMyTaskList(this.taskPackage.id);
	},
	onReachBottom: function() {		
		if (this.myTasklist.length < this.total) {
			this.pageIndex++;
			this.getMyTaskList(this.taskPackage.id);
		}
	},

	methods: {
		getMyTaskList(taskId) {
			console.log(taskId);
			var _this = this;
			var params = {
				pageIndex: _this.pageIndex,
				pageSize: _this.pageSize,
				state: _this.sendType
			};
			uni.request({
				url: _this.ApiGateWay + '/sale/Member/GetTaskDetails?taskId=' + taskId,
				data: params,
				method: 'POST',
				dataType: 'json',
				header: {
					Authorization: _this.token
				},
				success: function(res) {
					console.log(res);
					if (Helper.CheckData(res)) {
						let {
							errorCode,
							errorMessage,
							list,
							total,
							totalPage
						} = res;							
						_this.myTasklist = _this.myTasklist.concat(res.data.list);

						// _this.setListStatus();
					} else {

					}


				},

				fail: function() {
					uni.hideLoading();
				},
				complete: function() {
					uni.hideLoading();
					uni.stopPullDownRefresh()
				}
			});
		},
		showImg(taskPic){
			
			// this.isOk="block"
			// this.img=taskPic
			if(taskPic==null||taskPic.length==0){
				console.log(1)
				uni.showToast({
					title: "没有凭证",
					icon: "none"
				})
			}
			else{	
				
				this.isOk="block"				
				var num=taskPic.indexOf("task");
		
				if (taskPic.indexOf("task") > 0) {
					this.textTishi="图片已失效"
					this.img=""	
				}else{						
					this.img=taskPic	
				}					
			}
		}
		,closeImg(){
			this.isOk="none";
		}
	}
}
